<template>
  <div>
    <h1> </h1>
    <div>
      <v-btn @click="toastTest1"> Info</v-btn>
      <v-btn @click="toastTest2"> Success</v-btn>
      <v-btn @click="toastTest3"> Error</v-btn>
      <v-btn @click="toastTest4"> Warning</v-btn>
      <v-btn @click="toastTest5"></v-btn>
    </div>
    <h1> </h1>
    <div>
      <v-btn @click="barTest1">Start</v-btn>
      <v-btn @click="barTest2">Finish</v-btn>
      <v-btn @click="barTest3">Fail</v-btn>
    </div>
    <h1>Notify </h1>
    <div>
      <v-btn @click="notifyTest1">Alert</v-btn>
      <v-btn @click="notifyTest2">Confirm</v-btn>
      <v-btn @click="notifyTest3">Prompt</v-btn>
    </div>
    <h1>Axios </h1>
    <div>
      <v-btn @click="axiosTest1">Test</v-btn>
      <v-btn @click="axiosTest2">Error</v-btn>
    </div>
    <h1>Socket </h1>
    <div>
      <v-btn @click="joinRoom('testroom')"> </v-btn>
      <v-btn @click="leaveRoom('testroom')"> </v-btn>
      <v-btn @click="sendMsg1"> </v-btn>
      <v-btn @click="sendMsg2">  </v-btn>
      <v-btn @click="sendMsg3"> </v-btn>
      <v-btn @click="sendMsg4">  </v-btn>
    </div>
    <div>{{ $store.state.config.test1 }}</div>
    <h1>Chat </h1>
    <div>
      <v-text-field v-model="toId" label=""></v-text-field>
      <v-text-field v-model="chatMsg" label=""></v-text-field>
      <v-btn @click="chatTest"> </v-btn>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  name: "Home",
  data() {
    return {
      title: "My App",
      toId: "",
      chatMsg: "",
    };
  },
  title() {
    return this.title;
  },
  socket() {
    return {
      "room:testmsg": (data) => {
        console.log("room:testmsg", data);
      },
      "chat:test": (data) => {
        console.log("chat:test", data);
      },
    };
  },
  methods: {
    ...mapActions("socket", ["joinRoom", "leaveRoom"]),
    toastTest1() {
      this.$toast.info("Hello Info");
    },
    toastTest2() {
      this.$toast.success("Hello success");
    },
    toastTest3() {
      this.$toast.error("Hello error");
    },
    toastTest4() {
      this.$toast.warning("Hello warning");
    },
    toastTest5() {
      throw new Error(" ");
    },
    barTest1() {
      this.$Progress.start();
    },
    barTest2() {
      this.$Progress.finish();
    },
    barTest3() {
      this.$Progress.fail();
    },
    async notifyTest1() {
      const res = await this.$ezNotify.alert(" .", "", {
        icon: "mdi-video-4k-box",
      });
      console.log(res);
    },
    async notifyTest2() {
      const res = await this.$ezNotify.confirm(" .", "");
      console.log(res);
    },
    async notifyTest3() {
      const res = await this.$ezNotify.prompt(
        " .",
        "",
        { width: 200 }
      );
      console.log(res);
    },
    async axiosTest1() {
      const result = await this.$axios.get("/api/member/test");
      console.log(result);
    },
    async axiosTest2() {
      const result = await this.$axios.get("/api/error");
      console.log(result);
    },
    sendMsg1() {
      this.$socket.emit("room:msg", {
        msg: "    .",
        target: 1,
      });
    },
    sendMsg2() {
      this.$socket.emit("room:msg", {
        msg: "     .",
        target: 2,
      });
    },
    sendMsg3() {
      this.$socket.emit("room:msg", {
        msg: "     .",
        target: 3,
      });
    },
    sendMsg4() {
      this.$socket.emit("room:msg", {
        msg: "     .",
        target: 4,
      });
    },
    chatTest() {
      const { toId, chatMsg } = this;
      const { member } = this.$store.state.user;
      if (member) {
        this.$socket.emit("chat:test", {
          toId,
          chatMsg,
          fromId: member.mb_id,
        });
      }
    },
  },
};
</script>
